import React from 'react';
import { useHistory } from 'react-router-dom';
import Logo from '../../assets/logo-white.png';
import Footer from '../Footer';
export default function Attendance() {
    const history = useHistory();
    async function avaliaruim() {

        try {
            localStorage.setItem('atendimento', 'ruim');
            history.push('/compare');
        }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    async function avaliabom() {

        try {
            localStorage.setItem('atendimento', 'bom');
            history.push('/compare');
        }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    async function avaliaotimo() {

        try {
            localStorage.setItem('atendimento', 'otimo');
            history.push('/compare');
        }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    return (
        <div className="container">
            <div className=" margem">
                <div className="form-group text-center">
                    <img className="logo" src={Logo} alt="Axes Farma"></img>
                    <div className="col">
                        <h1>Como foi o nosso atendimento?</h1>
                    </div>
                    <br></br>
                    <br></br>
                    <div className=" d-flex justify-content-center">
                        <div className="form-row ">
                            <div className="col-md-4 mt-3">
                                <button type="button" className="btn btn-outline-text tamanho " onClick={() => avaliaruim()}>
                                    Ruim
                            </button>
                            </div>
                            <div className="col-md-4 mt-3">
                                <button type="button" className="btn btn-outline-text tamanho " onClick={() => avaliabom()}>
                                    Bom
                            </button>
                            </div>
                            <div className="col-md-4 mt-3">
                                <button type="button" className="btn btn-outline-text tamanho " onClick={() => avaliaotimo()}>
                                    Ótimo
                            </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <Footer></Footer>
        </div>
    )
}