import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import api from '../../services/api';
import Navbar from '../Navbar';
export default function CreateUser() {
    const [name, setName] = useState([]);
    const [email, setEmail] = useState([]);
    const history = useHistory();
    const log_id = localStorage.getItem("user");
    if(!log_id){
        history.push('/login');
      }
    async function handleCreate(e) {
        e.preventDefault();
        const data = {
            name,
            email
        }
        try {
            await api.post('api/user/new', data);
            alert('Usuário cadastrado com sucesso!');
            history.push('/Searchs');
        }
        catch (err) {
            alert('Erro ao cadastrar usuário');
        }
    }
    return (
        <div className="log-container">
            <Navbar></Navbar>
            <div className="container">
                <div className="center2">
                    <br></br><br></br><br></br>
                    <div className="card shadow">
                        <div className="card-body">
                            <form onSubmit={handleCreate}>
                                <div className="form-group">
                                    <label className="font-form">Nome do Colaborador</label>
                                    <input value={name} onChange={e => setName(e.target.value)} className="form-control" required></input>
                                </div>
                                <div className="form-group">
                                    <label className="font-form">E-mail</label>
                                    <input type="email" value={email} onChange={e => setEmail(e.target.value)} className="form-control" required></input>
                                </div>
                                <div className="text-center">
                                    <button className="btn btn-outline-orange" type="submit"> Cadastrar </button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}