import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom';
import api from '../../services/api';
import Navbar from '../Navbar';
import './styles.css';
export default function Searchs() {
    const history = useHistory();
    const [posts, setNaoEncontrados] = useState([]);
    const log_id = localStorage.getItem("user");
    if (!log_id) {
        history.push('/login');
    }
    useEffect(() => {
        api.get('api/search/notFoundProducts')
            .then(response => {
                setNaoEncontrados(response.data);
            });
    }, [log_id]);
    return (
        <div className="Log-container">
            <Navbar></Navbar>
            <div className="container">
                <br></br>
                <br></br>
                <table className="table table-bordered shadow">
                    <thead className="thead-orange">
                        <tr className="text-center">
                            <th>Produtos que não foram encontrados em estoque</th>
                        </tr>
                    </thead>
                    <tbody>
                        {posts.map((post) => (
                            <tr>
                                <td key={post.id} className="text-center">{post.nao_encontrados}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    )
}