import React, {useEffect, useState} from 'react'
import { useHistory } from 'react-router-dom';
import api from '../../services/api';
import Navbar from '../Navbar';
import './styles.css';
export default function Searchs() {
    const history = useHistory();
    const [produtosEncontrados,setProdutosEncontrados] = useState(0);
    const [nao_encontrados,setNaoEncontrados] = useState(0);
    const [atendOtimo,setAtendOtimo] = useState(0);
    const [atendBom,setAtendBom] = useState(0);
    const [atendRuim,setAtendRuim] = useState(0);
    const [preco_justo,setPrecoJusto] = useState(0);
    const [preco_caro,setPrecoCaro] = useState(0);
    const [preco_igual,setPrecoIgual] = useState(0);
    const log_id = localStorage.getItem("user");
    if(!log_id){
        history.push('/login');
      }
    useEffect(() => {
        api.get('api/search/info')
            .then(response => {
                console.log(response.data);
                setProdutosEncontrados(response.data.produtos_encontrados);
                setNaoEncontrados(response.data.produtos_nao_encontrados);
                setAtendOtimo(response.data.atendimento_otimo);
                setAtendBom(response.data.atendimento_bom);
                setAtendRuim(response.data.atendimento_ruim);
                setPrecoJusto(response.data.preco_justo);
                setPrecoCaro(response.data.preco_caro);
                setPrecoIgual(response.data.preco_igual);
            });
    }, [log_id]);

    return (
        <div className="Log-container">
            <Navbar></Navbar>
            <div className="container">
                <br></br>
                <br></br>
                <table className="table table-bordered shadow">
                    <thead className="thead-orange">
                        <tr className="text-center">
                            <th>Foi encontrado todos os produtos ou medicamentos que os clientes</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">Sim</th>
                                <td>{produtosEncontrados}</td>
                        </tr>
                        <tr>
                            <th scope="row row-sm">Não</th>
                            <td>{nao_encontrados}</td>
                        </tr>
                    </tbody>
                </table>
                <br></br>
                <table className="table table-bordered shadow">
                    <thead className="thead-orange">
                        <tr className="text-center">
                            <th>Como foi o nosso atendimento</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">Ótimo</th>
                            <td>{atendOtimo}</td>
                        </tr>
                        <tr>
                            <th scope="row ">Bom</th>
                            <td>{atendBom}</td>
                        </tr>
                        <tr>
                            <th scope="row ">Ruim</th>
                            <td>{atendRuim}</td>
                        </tr>
                    </tbody>
                </table>

                <br></br>
                <table className="table table-bordered shadow">
                    <thead className="thead-orange">
                        <tr className="text-center">
                            <th>O que os clientes acharam dos nossos preços</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <th scope="row">Preço justo</th>
                            <td>{preco_justo}</td>
                        </tr>
                        <tr>
                            <th scope="row ">Muito caro</th>
                            <td>{preco_caro}</td>
                        </tr>
                        <tr>
                            <th scope="row ">Igual ao de outras drogarias</th>
                            <td>{preco_igual}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}