import React from 'react';
import { useHistory } from 'react-router-dom';
import Logo from '../../assets/logo-white.png';
import Footer from '../Footer';
export default function Evaluate() {
    const history = useHistory();
    
    async function encontrou() {
     
        try {
            localStorage.setItem('encontrado', 1);
            localStorage.setItem('nao_encontrados','produto encontrado');
            history.push('/attendance');
        }
        catch {
            alert('Erro ao processar resposta, tente novamente mais tarde');
        }
    }
    async function nencontrou() {
        try {
            localStorage.setItem('encontrado', 0);
            history.push('/info');
        }
        catch {
            alert('Erro ao processar resposta, tente novamente mais tarde');
        }
    }
    return (
        <div className="container">
            <div className="d-flex justify-content-center margem">

                <div className="form-group text-center">
                    <img className="logo" src={Logo} alt="Axes Farma"></img>
                    <div className="col">
                        <h1>Você encontrou os produtos ou medicamentos que procurava?</h1>
                    </div>
                    <br></br>

                    <button type="button" className="btn btn-outline-text tamanho" onClick={() => encontrou()}>
                        Sim
                        </button>
                    <button type="button" className="btn btn-outline-text tamanho ml-5" onClick={() => nencontrou()}>
                        Não
                    </button>
                </div>

            </div>
            <Footer></Footer>
        </div>
    )
}