import React from 'react';
import api from '../../services/api';
import { useHistory } from 'react-router-dom';
import Logo from '../../assets/logo-white.png';
import Footer from '../Footer';
import './styles.css';
export default function Compare() {
    const history = useHistory();
    const encontrou_produto = localStorage.getItem('encontrado');
    const nao_encontrados = localStorage.getItem('nencontrados');
    const avaliacao_atendimento = localStorage.getItem('atendimento');
    async function avaliajusto() {
        const avaliacao_preco = 'justo';
        const data = {
            encontrou_produto,
            nao_encontrados,
            avaliacao_atendimento,
            avaliacao_preco
        }
        try {
            await api.post('api/search/new',data);
            history.push('/final');
        }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    async function avaliacaro() {
        const avaliacao_preco = 'caro';
        const data = {
            encontrou_produto,
            nao_encontrados,
            avaliacao_atendimento,
            avaliacao_preco
        }
        try {
            await api.post('api/search/new',data);
            history.push('/final');
        }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    async function avaliamedio() {
        const avaliacao_preco = 'igual';
        const data = {
            encontrou_produto,
            nao_encontrados,
            avaliacao_atendimento,
            avaliacao_preco
        }
         try {
            await api.post('api/search/new',data);
             history.push('/final');
         }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    return (
        <div className="container">
            <div className="d-flex justify-content-center margem">
                <div className="form-group text-center">
                    <img className="logo" src={Logo} alt="Axes Farma"></img>
                    <div className="col">
                        <h1>O que achou dos nossos preços?</h1>
                    </div>
                    <br></br>
                    <br></br>
                    <button type="button" className="btn btn-outline-text tamanho" onClick={() => avaliajusto()}>
                        Preço justo
                    </button>
                    <button type="button" className="btn btn-outline-text tamanho marg" onClick={() => avaliacaro()}>
                        Muito caro
                    </button>
                    <br></br>
                    <br></br><br></br>
                    <div className="col">
                        <button type="button" className="btn btn-outline-text tamanho2 " onClick={() => avaliamedio()}>
                            igual ao de outras drogarias
                        </button>
                    </div>

                </div>
            </div>
            <Footer></Footer>
        </div>
    )
}