import React, { Component } from 'react';
import { NavLink, Link} from 'react-router-dom';
import Logo from '../../assets/logo-white.png';
import { Navbar, Nav, Image } from 'react-bootstrap';
import { BiLogOut } from 'react-icons/bi';
import './styles.css';
class Barra extends Component {
    render() {
        return (
            <Navbar expand="lg" className="NavColor">

                <Link to="/searchs"><Image src={Logo} className="Logo" fluid /></Link>

                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <NavLink to="/products" className="font-nav nav">Produtos não encontrados</NavLink>
                        <NavLink to="/create" className="font-nav nav">Novo cadastro</NavLink>
                    </Nav>
                    <Link to="/login"><BiLogOut size={30} color="white" /></Link>
                </Navbar.Collapse>
            </Navbar>
        );
    }
}

export default Barra;