import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import api from '../../services/api';
import { Image, Card, Form, Button, Container } from 'react-bootstrap';
import Footer from '../Footer';
import LogoAxes from '../../assets/logo.png';
export default function Senha() {
    const [password, setPassword] = useState('');
    const [password_confirmation, setPassword_confirmation] = useState('');
    const history = useHistory();
    const id = localStorage.getItem('id');
    if(!id){
        history.push('/login');
    }
    async function handleSenha(e) {
        e.preventDefault();
        try {
            await api.post('api/firstAccess', {id, password, password_confirmation });
            alert('Senha alterada com Sucesso');
            history.push('/login');
        } catch (err) {
            alert('Senhas digitadas não conferem');
        }
    }

    return (
        <Container className="center1">
            <Card className="wd-card Card">
                <Card.Body>
                    <Form onSubmit={handleSenha}>
                        <Form.Group className="TextCenter">
                            <Image src={LogoAxes} className="LogoLogin" fluid />
                        </Form.Group>
                        <Form.Group controlId="formBasicEmail" className="Form-mg">
                            <Form.Label className="font-form">Nova senha</Form.Label>
                            <input className="form-control" type="password" minLength="8" maxLength="150" value={password} onChange={e => setPassword(e.target.value)} required></input>
                        </Form.Group>
                        <Form.Group controlId="formBasicPassword">
                            <Form.Label className="font-form">Confirme sua senha</Form.Label>
                            <input className="form-control" type="password" minLength="8" value={password_confirmation} onChange={e => setPassword_confirmation(e.target.value)} required></input><br />
                        </Form.Group>
                        <Form.Group className="TextCenter">
                            <Button variant="btn btn-outline-orange tamanho"  type="submit">Cadastrar</Button>
                        </Form.Group>
                    </Form>
                </Card.Body>
            </Card>
            <Footer></Footer>
        </Container>
    );

}