import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import Logo from '../../assets/logo-white.png';
import Footer from '../Footer';
export default function Home() {
    const [text, setText] = useState([]);
    const history = useHistory();
    async function informacao(e) {
        e.preventDefault();
        try {
            localStorage.setItem('nencontrados', text);
            history.push('/attendance');
        }
        catch {
            alert('Não foi possivel processar a resposta, tente novamente mais tarde');
        }
    }
    return (
        <div className="container">
            <div className="d-flex justify-content-center margem">
                <div className="form-group text-center">
                    <img className="logo" src={Logo} alt="Axes Farma"></img>
                    <div className="col">
                        <h1>Quais medicamentos ou produtos não encontrou?</h1>
                    </div>
                    <br></br>
                    <form onSubmit={informacao}>
                        <input
                            value={text}
                            type="text"
                            name="cad" 
                            className="form-control shadow"
                            onChange={e => setText(e.target.value)}
                            required    
                        />
                        <br></br>

                        <button type="submit" className="btn-outline-text btn tamanho">
                            Prosseguir
                        </button>
                    </form>
                </div>
            </div>
            <Footer></Footer>
        </div>
    )
}