import React, { useState } from 'react';
import { useHistory} from 'react-router-dom';
import './style.css';
import api from '../../services/api';
import { Image, Card, Form, Container } from 'react-bootstrap';
import LogoAxes from '../../assets/logo.png';
import Footer from '../Footer';

export default function Login() {
    const [username, setuserName] = useState('');
    const [password, setPassword] = useState('');
    const history = useHistory();
    localStorage.clear()
    async function handleLogin(e) {
        e.preventDefault();
        try {
            const response = await api.post('api/login', { username, password });
            if (response.status === 200) {
                if(password === "mudar123"){
                    alert('É necessário a alteração de senha');
                    localStorage.setItem('id',response.data[0].id);
                    history.push('/changepass')
                }
                else{
                    localStorage.setItem('user', response.data[0].username);
                    history.push('/searchs');
                }
            }
        } catch (err) {
            if (err.request.status === 404) {
                alert('Usuário não cadastrado');
            }
            else if (err.request.status === 400) {
                alert('Usuário/Senha incorretos!');
            }
        }
    }
    return (
        <Container>
            <div className="center1">
                <Card className="wd-card Card">
                    <Card.Body>
                        <form onSubmit={handleLogin}>
                            <div className="form-group TextCenter">
                                <Image src={LogoAxes} className="LogoLogin" fluid />
                            </div>
                            <div className="form-group Form-mg">
                                <Form.Label className="font-form">Usuário</Form.Label>
                                <input className="form-control" type="text" placeholder="Usuário" value={username} onChange={e => setuserName(e.target.value)}></input>
                            </div>
                            <div className="form-group">
                                <Form.Label className="font-form">Senha</Form.Label>
                                <input className="form-control" type="password" placeholder="Senha" value={password} onChange={e => setPassword(e.target.value)}></input><br />
                            </div>

                            <div className="form-group TextCenter">
                                <button className="btn btn-outline-orange tamanho" type="submit">Entrar</button>
                            </div>
                        </form>
                    </Card.Body>
                </Card>
            </div>
            <Footer></Footer>
        </Container>
    );
}