import React from 'react';
import { Link } from 'react-router-dom';
import './styles.css';
import Logo from '../../assets/logo-white.png';
import Footer from '../Footer';
export default function Home() {
    localStorage.clear();
    return (
        <div className="container">
            <div className="d-flex justify-content-center margem">
                <div className="form-group text-center">
                    <img className="logo" src = {Logo} alt="Axes Farma"></img>
                    <div className="col">
                        <h1>Queremos saber a sua opinião!</h1>
                    </div>
                    <h4>leva só alguns segundos</h4>
                    <br></br>
                    <Link to="/avalia">
                        <button className="btn-outline-text btn  tamanho">
                            Iniciar Pesquisa
                        </button>
                    </Link>
                </div>
            </div>
            <Footer></Footer>
        </div>
    )
}